<template>
    <div class="c-banner">
        <div class="banner-container">
            <div class="swiper-wrapper" id="banner">
				<div class="swiper-slide" v-for="item in swiper">
                    <a :href="item.link ||  'javascript:;'"><img :src="ApiConfig.domain + item.image" alt=""></a>
                </div>
			</div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import Swiper from 'swiper';

export default {
	name: "BannerComponent",
	props:['swiper'],
	mounted: function(){
		this.$nextTick(function(){
			new Swiper('.banner-container', {
				autoplay: {
					delay: this.ApiConfig.banner_delay,//1
				},//
				loop : true,
				pagination: {
					el: '.swiper-pagination',
					bulletClass : 'my-bullet',
					bulletActiveClass: 'my-bullet-active',
				},
				observer:true,      //swiperswiper
				observeParents:true,//swiperswiper
			})
		})
	}
};
</script>



